import React, { useEffect, useState } from 'react';
import { useUser } from '../../components/UserContext';
import {
  fetchEducators,
  addEducator,
  editEducator,
  deleteEducator,
} from '../../services/superAdminApi';
import { AnimatedMascotsLoader, ErrorMsg, DataTable, DataTableColumn, TableCard, ModernSearchBar, ModernSearchBarWrapper, MobileSearchStack } from '../../components/ui/AdminDashboardUI';
import Header from '../../components/Header';
import SearchIcon from '../../components/ui/SearchIcon';
import ModernModal from '../../components/ui/ModernModal';
import { FormField, Label, Input, ErrorText, ModalButton } from '../../components/ui/ModernModal';
import styled from 'styled-components';
import { fetchGroups } from '../../services/adminApi';

const AddButton = styled.button`
  background: ${({ theme }) => theme.colors.accent};
  color: #212121;
  font-size: 17px;
  font-weight: 700;
  padding: 12px 28px;
  border-radius: 14px;
  box-shadow: 0 2px 10px rgba(255,193,7,0.10);
  border: none;
  margin-left: 0;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: background 0.18s, box-shadow 0.18s, transform 0.18s, opacity 0.18s;
  opacity: 0.97;
  will-change: transform, opacity;
  &:hover {
    background: ${({ theme }) => theme.colors.primary};
    color: #fff;
    transform: scale(1.045);
    opacity: 1;
    box-shadow: 0 8px 32px rgba(76,175,80,0.16);
  }
  @media (max-width: 700px) {
    width: 100%;
    margin-top: 10px;
    justify-content: center;
    font-size: 16px;
    padding: 12px 0;
  }
`;

const SearchBarArea = styled.div`
  display: flex;
  align-items: center;
  gap: 18px;
  width: 100%;
`;

const SearchIconStyled = styled(SearchIcon)`
  margin-right: 10px;
`;

const Educators: React.FC = () => {
  const { benutzer } = useUser();
  const [educators, setEducators] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [search, setSearch] = useState('');
  const [modalOpen, setModalOpen] = useState(false);
  const [form, setForm] = useState({ name: '', email: '', password: '' });
  const [formError, setFormError] = useState<string | null>(null);
  // Pagination
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [groups, setGroups] = useState<any[]>([]);
  const [selectedGroupIds, setSelectedGroupIds] = useState<string[]>([]);

  const load = async () => {
    setLoading(true);
    setError(null);
    try {
      const data = await fetchEducators();
      setEducators(data);
    } catch (e: any) {
      setError(e.message || 'Fehler beim Laden der Erzieher.');
    }
    setLoading(false);
  };

  useEffect(() => {
    if (benutzer?.role !== 'SUPER_ADMIN') return;
    load();
    // Fetch all groups for group assignment
    fetchGroups().then(setGroups);
    // eslint-disable-next-line
  }, [benutzer]);

  const handleAdd = async () => {
    if (!form.name || !form.email || !form.password) {
      setFormError('Name, E-Mail und Passwort sind erforderlich.');
      return;
    }
    setLoading(true);
    setFormError(null);
    try {
      await addEducator({ name: form.name, email: form.email, password: form.password, groupIds: selectedGroupIds });
      setModalOpen(false);
      setForm({ name: '', email: '', password: '' });
      setSelectedGroupIds([]);
      await load();
    } catch (e: any) {
      setFormError(e.message || 'Fehler beim Hinzuf√ºgen.');
    }
    setLoading(false);
  };

  const handleEdit = async (row: any) => {
    setLoading(true);
    try {
      await editEducator(row.id, {
        name: row.name,
        email: row.email,
        password: row.password || undefined, // only send if changed
      });
      await load();
    } catch (e: any) {
      setError(e.message || 'Fehler beim Bearbeiten.');
    }
    setLoading(false);
  };

  const handleDelete = async (row: any) => {
    setLoading(true);
    try {
      await deleteEducator(row.id);
      await load();
    } catch (e: any) {
      setError(e.message || 'Fehler beim L√∂schen.');
    }
    setLoading(false);
  };

  if (benutzer?.role !== 'SUPER_ADMIN') return <ErrorMsg>Zugriff verweigert</ErrorMsg>;
  if (loading) return <AnimatedMascotsLoader text="Lade Erzieher..." />;
  if (error) return <ErrorMsg>{error}</ErrorMsg>;

  const columns: DataTableColumn<any>[] = [
    { key: 'name', label: 'Name', editable: true, filterable: true, sortable: true },
    { key: 'email', label: 'E-Mail', editable: true, filterable: true, sortable: true },
    { key: 'password', label: 'Passwort (neu setzen)', editable: true },
    { key: 'id', label: 'Erzieher-ID', editable: false, filterable: true, sortable: true },
  ];

  return (
    <main style={{ maxWidth: 1100, margin: '0 auto', padding: '32px 0 0 0', display: 'flex', flexDirection: 'column', alignItems: 'stretch' }}>
      <Header title="Erzieher-√úbersicht" />
      <TableCard>
        <div style={{ display: 'flex', flexDirection: 'column', gap: 0, marginBottom: 18 }}>
          <span style={{ fontSize: '1.25em', fontWeight: 700, color: '#4CAF50', marginBottom: 8 }}>Erzieher</span>
          <MobileSearchStack>
            <ModernSearchBarWrapper style={{ flex: 1, width: '100%' }}>
              <ModernSearchBar
                type="text"
                placeholder="Suchen..."
                value={search}
                onChange={e => setSearch(e.target.value)}
                aria-label="Suchen"
              />
              <SearchIconStyled />
            </ModernSearchBarWrapper>
            <AddButton onClick={() => setModalOpen(true)}>
              + Hinzuf√ºgen
            </AddButton>
          </MobileSearchStack>
        </div>
        <DataTable
          data={educators}
          columns={columns}
          onEdit={handleEdit}
          onDelete={handleDelete}
          entityName="Erzieher"
          search={search}
          page={page}
          rowsPerPage={rowsPerPage}
          onPageChange={setPage}
          onRowsPerPageChange={rows => { setRowsPerPage(rows); setPage(0); }}
          totalCount={educators.length}
        />
      </TableCard>
      <ModernModal open={modalOpen} onClose={() => { setModalOpen(false); setFormError(null); }} title="Erzieher/in hinzuf√ºgen">
        {formError && <ErrorText>{formError}</ErrorText>}
        <form onSubmit={e => { e.preventDefault(); handleAdd(); }}>
          <FormField>
            <Label htmlFor="educator-name">Name</Label>
            <Input id="educator-name" value={form.name} onChange={e => setForm(f => ({ ...f, name: e.target.value }))} autoFocus required />
          </FormField>
          <FormField>
            <Label htmlFor="educator-email">E-Mail</Label>
            <Input id="educator-email" type="email" value={form.email} onChange={e => setForm(f => ({ ...f, email: e.target.value }))} required />
          </FormField>
          <FormField>
            <Label htmlFor="educator-password">Passwort</Label>
            <Input id="educator-password" type="password" value={form.password} onChange={e => setForm(f => ({ ...f, password: e.target.value }))} required />
          </FormField>
          <FormField>
            <Label htmlFor="educator-groups">Gruppen</Label>
            <select id="educator-groups" multiple value={selectedGroupIds} onChange={e => setSelectedGroupIds(Array.from(e.target.selectedOptions).map(o => o.value))} style={{ width: '100%', padding: 10, borderRadius: 8, border: '1.5px solid #E0E0E0' }}>
              {groups.map((g: any) => (
                <option key={g.id} value={g.id}>{g.name}</option>
              ))}
            </select>
          </FormField>
          <ModalButton type="submit">Hinzuf√ºgen</ModalButton>
        </form>
      </ModernModal>
    </main>
  );
};

export default Educators; 