/**
 * Neue Institution anlegen
 */
export async function addKita(data: { name: string; address: string }): Promise<any> {
  const res = await fetch(`${API_URL}/institutionen`, {
    method: 'POST',
    credentials: 'include',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data),
  });
  if (!res.ok) throw new Error('Fehler beim Anlegen der Institution');
  return res.json();
}

