/**
 * Alle Plattform-Admins abrufen
 */
export async function fetchPlatformAdmin(): Promise<any[]> {
  const res = await fetch(`${API_URL}/users?role=SUPER_ADMIN`, { credentials: 'include' });
  if (!res.ok) throw new Error('Fehler beim Laden der Plattform-Admins');
  return res.json();
}

/**
 * Admin einer Kita zuweisen
 * @param {string} kitaId - Die ID der Kita
 * @param {string} adminId - Die ID des Admins
 */
export async function assignAdmin(kitaId: string, adminId: string): Promise<any> {
  const res = await fetch(`${API_URL}/institutions/${kitaId}/assign-admin`, {
    method: 'POST',
    credentials: 'include',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ adminId }),
  });
  if (!res.ok) throw new Error('Fehler beim Zuweisen des Admins');
  return res.json();
}

/**
 * Erzieher anlegen
 */
export async function addEducator(data: { name: string; email: string; password: string; groupIds?: string[] }): Promise<any> {
  const res = await fetch(`${API_URL}/users`, {
    method: 'POST',
    credentials: 'include',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ ...data, role: 'EDUCATOR' }),
  });
  if (!res.ok) throw new Error('Fehler beim Anlegen des Erziehers');
  return res.json();
}

/**
 * Erzieher bearbeiten
 */
export async function editEducator(id: string, data: { name?: string; email?: string; password?: string; groupIds?: string[] }): Promise<any> {
  const res = await fetch(`${API_URL}/users/${id}`, {
    method: 'PUT',
    credentials: 'include',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data),
  });
  if (!res.ok) throw new Error('Fehler beim Bearbeiten des Erziehers');
  return res.json();
}

/**
 * Erzieher l√∂schen
 */
export async function deleteEducator(id: string): Promise<any> {
  const res = await fetch(`${API_URL}/users/${id}`, {
    method: 'DELETE',
    credentials: 'include',
  });
  if (!res.ok) throw new Error('Fehler beim L√∂schen des Erziehers');
  return res.json();
}

